html,body{width:100%;height:100%;margin:0;padding:0;overflow:hidden;position:fixed;background:#111}html{-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}h1,h2,h3,h4,text,p,button{-webkit-font-smoothing:antialiased;-moz-osx-webkit-font-smoothing:antialiased;font-family:"Open Sans", sans-serif}video{top:0;left:0;position:absolute}.Main-Canvas{user-select:none;opacity:1;transition:opacity 0.5s ease-out 0.25s}.isLoading .Main-Canvas{opacity:0}.container{display:flex;width:100%;height:100%;position:fixed;top:0;left:0;z-index:98;justify-content:center;align-items:center}.Message{position:absolute;z-index:999;width:100%;top:calc(50% - 50px);text-align:center;letter-spacing:11px;color:white;opacity:0;transition:opacity 0.5s ease-out, letter-spacing 0.5s ease-out;pointer-events:none}.isLoading .Message{letter-spacing:10px;opacity:1}.Loading-Bar{position:absolute;z-index:998;width:0%;height:1px;top:50%;background:rgba(255,255,255,0.5);opacity:0;transition:width 0.5s ease-out, opacity 0.5s ease-out}.isLoading .Loading-Bar{opacity:1}.env{position:fixed;width:100%;z-index:9999;color:white;top:100px;font-size:24px;padding:20px}.container-button{position:absolute;width:100%;height:100%;display:flex;align-items:center;justify-content:center}.btnAR{margin-top:120%;width:160px;height:40px;border-radius:20px;transition:opacity 0.5s ease-out}.isLoading .btnAR{transition:opacity 0s ease-out;opacity:0}.no-xr .btnAR{display:none}.xr-started .btnAR{display:none}.text-no-support{color:white;font-size:14px;letter-spacing:16px;text-align:center;display:none}.no-xr .text-no-support{display:block}.top-layer{z-index:99}
